@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@700&display=swap);
html,
body,
h1,
h2,
h3,
p,
ul,
li {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  border: none;
  vertical-align: bottom;
  max-width: 100%;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: 'M PLUS Rounded 1c', sans-serif;
  font-size: 1.8rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eeffff;
  overflow-y: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  max-width: 100%;
}

.App {
  text-align: center;
  width: 90%;
  margin: 0 auto;
  height: 100vh;
  position: relative;
}

.pokemonCardConteainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
  margin-top: 20px;
  place-items: center;
}

.btn {
  padding: 30px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}

.btn button {
  padding: 13px 32px;
  background: linear-gradient(#e40920, #ff5555);
  border: none;
  box-shadow: 5px 5px 20px -3px #777777;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  transition: all 0.1s;
}

.btn button:hover {
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
  box-shadow: none;
}

.targetItem {
  width: 200px;
  height: 200px;
}
img {
  margin: 0 auto;
}

.card {
  width: 290px;
  height: 290px;
  box-shadow: 5px 5px 3px 0px #777777;
  border-radius: 10px;
  background-color: white;
  cursor: pointer;
  transition: all 0.1s;
}

.card:hover {
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}

.cardName {
  padding: 0;
  font-size: 24px;
  margin-bottom: 10px;
  margin-top: 0;
}

.cardInfo {
  text-align: center;
}

.cardImg {
  width: 50%;
  display: flex;
  margin: 0 auto;
}

nav {
  background: linear-gradient(#e40920, #ff5555);
  color: white;
  height: 80px;
  font-weight: 600px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
  flex-flow: column;
}

.modalClose {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 15px;
  top: 15px;
  width: 40px;
  height: 40px;
  border: 2px solid white;
  border-radius: 50%;
  background-color: #0028db;
  cursor: pointer;
}

.modalClose:hover {
  -webkit-transform: scale(1.05,1.05);
          transform: scale(1.05,1.05);
}

.modalClose::before, .modalClose::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 4px;
  height: 22px;
  background: white;
}

.modalClose::before {
  -webkit-transform: translate(-50%,-50%) rotate(45deg);
          transform: translate(-50%,-50%) rotate(45deg);
}

.modalClose::after {
  -webkit-transform: translate(-50%,-50%) rotate(-45deg);
          transform: translate(-50%,-50%) rotate(-45deg);
}

.modalContent {
  background: linear-gradient(#0028db, #0097db);
  color: white;
  font-weight: 600px;
  padding: 10px;
  border-radius: 25px;
  display: flex;
  position: relative;
  width: 45%;
  padding: 50px 100px;
  border: 4px solid black;
}

h3 {
  font-size: 32px;
}

.modalImg {
  flex-grow: 1;
  width: 45%;
  display: flex;
  align-items: center;
}

.modalImg img {
  width: 80%;
  margin: 0 auto;
}

.modalInfo {
  flex-grow: 1;
  display: flex;
  flex-flow: column;
  width: 55%;
  height: 100%;
  text-align: left;
}

.pokemonType span{
  display: inline-block;
  background-color: aqua;
  margin-right: 10px;
  padding-right: 8px;
  padding-left: 8px;
  border-radius: 25px;
}

.pokemonIndex {
  text-transform: full-width;
}


.pokemonExplanation {
  margin-top: 40px;
  font-size: 22px;
  align-items: flex-end;
  white-space: pre-wrap;
}
